@font-face {
    font-family: "Helvetica Neue";
    src: url(../../assets/HelveticaNeue-Medium-11.ttf) format("truetype");
}

@font-face {
    font-family: "Helvetica Neue Bold";
    src: url(../../assets/HelveticaNeue-Bold-02.ttf) format("truetype");
}

.group {
    margin-left: 24px;
    margin-top: 24px;
}

.group .title {
    font-family: 'Helvetica Neue';
    font-style: normal;
    font-weight: 500;
    font-size: 17px;
    line-height: 20px;

    color: var(--product_card_text);

    margin-bottom: 14px;
}

.products {
    padding-right: 24px;
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    grid-gap: 16px;
}

@keyframes product-fade-in {
    from {
        opacity: 0;
    }

    to {
        opacity: 1;
    }
}

.product {
    display: flex;
    flex-direction: column;
    text-align: center;
    align-items: center;
    box-sizing: border-box;
    padding-bottom: 10px;
    opacity: 0;
    height: 100%;
    width: 26vw;
    transition: opacity .1s ease-in-out;
    animation: product-fade-in .32s linear forwards;
}

.product .image {
    width: 100%;
    border-radius: 8px;
}

.product .title {
    width: 100%;
    height: 36px !important;
    font-family: 'Helvetica Neue';
    font-style: normal;
    font-weight: 500;
    font-size: 15px;
    line-height: 15px;
    text-align: center;

    color: var(--product_card_text);

    margin-top: 15px;
    margin-bottom: 8px;
}

.product .description {
    font-family: 'Helvetica Neue';
    font-style: normal;
    font-weight: 500;
    font-size: 12px;
    line-height: 11px;
    /* identical to box height */

    text-align: center;

    color: var(--product_card_text);
}

.product .button {
    margin-right: 10px;
    margin-left: 10px;
    background: var(--button_color);
    border-radius: 4px;
    font-family: 'Helvetica Neue Bold';
    font-style: normal;
    font-weight: 500;
    font-size: 13px;
    line-height: 13px;

    color: var(--button_text);
    height: 30px;
    justify-content: center;
    display: flex;
    text-align: center;
    align-items: center;
    width: 100%;
    margin-top: 16px;
    outline: none;
    cursor: pointer;
}

.product .cart {
    margin-top: 16px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
}

.product .cart .unadd {
    outline: none;
    margin-right: 10px;
    margin-left: 10px;
    background: #DE3F3F;
    border-radius: 50%;
    height: 30px;
    width: 30px;
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
}

.product .cart .add {
    outline: none;
    margin-right: 10px;
    margin-left: 10px;
    background: var(--button_color);
    border-radius: 50%;
    height: 30px;
    width: 30px;
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
}

.product .cart .count {
    font-family: 'Helvetica Neue Bold';
    font-style: normal;
    font-weight: 500;
    font-size: 15px;
    line-height: 15px;
    /* identical to box height */

    text-align: center;

    color: var(--product_card_text);
}