@font-face {
    font-family: "Helvetica Neue Medium";
    src: url(../../assets/HelveticaNeue-Medium-11.ttf) format("truetype");
}

@-webkit-keyframes modal-fade-in {
    0% {
        opacity: 0
    }

    to {
        opacity: 1
    }
}

@keyframes modal-fade-in {
    0% {
        opacity: 0
    }

    to {
        opacity: 1
    }
}

.modal-wrapper {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    max-width: 100%;
    align-items: center;
    justify-content: center;
    background: rgba(51, 56, 61, 0.9);
    z-index: 1001;
    margin: 0;
    min-width: 100vw;
    min-height: 100vh;
    justify-content: center;
    align-items: center;
    display: flex;
    flex-direction: column;

    transition: opacity 167ms ease-out;
    -webkit-animation: modal-fade-in .32s ease;
    animation: modal-fade-in .32s ease;
}

.modal-content {
    max-width: 352px;
    background: var(--modal_background);
    border-radius: 8px;
    width: 100%;
    position: relative;
    min-height: 0;
    overflow: visible;
    overflow: initial;
    overflow-y: auto;

}

.modal-header {
    display: flex;
    justify-content: space-between;
    padding: 14px;
}

.modal-header img {
    float: right;
}

.modal-header>div {
    color: transparent;
}

.product-image {
    width: 100%;
}

.product-info {
    padding: 24px;
}

.product-info .title {
    font-family: 'Helvetica Neue Medium';
    font-style: normal;
    font-weight: 500;
    font-size: 23px;
    line-height: 25px;

    color: var(--modal_text);
}

.product-info .desc {
    font-family: 'Helvetica Neue Medium';
    font-style: normal;
    font-weight: 400;
    font-size: 16px;
    line-height: 18px;
    /* identical to box height, or 133% */


    color: var(--modal_text_secondary);

}

.productDescription {
    padding-right: 24px;
    padding-left: 24px;
    font-family: 'Helvetica Neue Medium';
    font-style: normal;
    font-weight: 400;
    font-size: 16px;
    line-height: 18px;
    /* identical to box height, or 133% */


    color: var(--modal_text_secondary);
}

.odnaZalupa {
    position: absolute;
    top: 24px;
    right: 24px;
    z-index: 9999999;
}

.vtorayaZalupa {
    position: relative;
    width: 100%;
}