body[theme="light"] {
  --background_page: #fff;

  --modal_background: #fff;
  --modal_text: #000;
  --modal_text_secondary: rgba(0, 0, 0, 0.64);
  --modal_cards_gradient: linear-gradient(90deg, rgba(0, 0, 0, 0.06) 0%, rgba(0, 0, 0, 0) 50%);

  --dropdown_background: #fff;
  --dropdown_border: #dadada;
  --dropdown_select_background: #dadada;
  --dropdown_select_text: #000;

  --input_background: #dadada;
  --input_color: #000;

  --scanner_text: #000;

  --button_color: #40C62B;
  --button_text_secondary: #000;
  --button_text: #fff;

  --product_card_text: #000;

  --destructive: #DE3F3F;
}

body[theme="dark"] {
  --background_page: #121920;

  --modal_background: #121920;
  --modal_text: #fff;
  --modal_text_secondary: rgba(255, 255, 255, 0.64);
  --modal_cards_gradient: linear-gradient(90deg, rgba(255, 255, 255, 0.06) 0%, rgba(255, 255, 255, 0) 50%);

  --dropdown_background: #121920;
  --dropdown_border: rgba(255, 255, 255, 0.1);
  --dropdown_select_background: rgba(255, 255, 255, 0.1);;
  --dropdown_select_text: #fff;

  --input_background: #2A3036;
  --input_color: #fff;

  --scanner_text: #fff;

  --button_color: #40C62B;
  --button_text_secondary: #fff;
  --button_text: #fff;

  --product_card_text: #fff;

  --destructive: #DE3F3F;
}


body {
  margin: 0;
  padding: 0;
  background: var(--background_page);
}

img {
  user-select: none;
}

.orderbutton {
  background: var(--button_color);
  font-family: 'Inter';
  font-style: normal;
  font-weight: 600;
  font-size: 16px;
  line-height: 19px;

  color: var(--button_text);
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 56px;
  position: absolute;
  top: calc(100vh - 56px);
}

li {
  font-family: 'Helvetica Neue';
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 18px;

  color: var(--modal_text_secondary);
  margin-bottom: 3px;
}

.nutritionals {
  padding-right: 24px;
  padding-left: 24px;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-gap: 18px;
}

.nutritional {
  padding: 9px;
  box-sizing: border-box;
  background: var(--modal_cards_gradient);
  border-radius: 5px;
}

.nutritional .title {
  font-family: 'Helvetica Neue Medium';
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 18px;
  /* identical to box height, or 133% */


  color: var(--modal_text_secondary);

  margin-bottom: 5px;
}

.nutritional .value {
  font-family: 'Helvetica Neue';
  font-style: normal;
  font-weight: 400;
  font-size: 19px;
  line-height: 21px;
  /* identical to box height, or 107% */


  color: var(--modal_text);

}

.buttons {
  margin-top: 24px;
  margin-bottom: 24px;
  width: calc(100% - 48px);
  display: flex;
  margin-right: 24px;
  margin-left: 24px;
  background: linear-gradient(90deg, rgba(255, 255, 255, 0.06) 0%, rgba(255, 255, 255, 0) 50%);
  border-radius: 8px;
}

.button1 {
  height: 40px;
  width: 50%;
  font-family: 'Helvetica Neue Medium';
  font-style: normal;
  font-weight: 500;
  font-size: 14px;
  line-height: 16px;
  display: flex;
  flex-direction: column;
  text-align: center;
  justify-content: center;
  color:var(--modal_text);
}

.button2 {
  height: 40px;
  background: var(--button_color);
  border-radius: 8px;
  font-family: 'Helvetica Neue Bold';
  font-style: normal;
  font-weight: 500;
  font-size: 13px;
  line-height: 13px;

  color: var(--button_text);
  justify-content: center;
  display: flex;
  text-align: center;
  align-items: center;
  width: 50%;
}


.info .cart {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
}

.info .cart .unadd {
  margin-right: 10px;
  margin-left: 10px;
  background: var(--destructive);
  border-radius: 50%;
  height: 40px;
  width: 40px;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
}

.info .cart .add {
  margin-right: 10px;
  margin-left: 10px;
  background: var(--button_color);
  border-radius: 50%;
  height: 40px;
  width: 40px;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
}

.info .cart .count {
  font-family: 'Helvetica Neue Bold';
  font-style: normal;
  font-weight: 500;
  font-size: 15px;
  line-height: 15px;
  /* identical to box height */

  text-align: center;

  color: #FFFFFF;
}

.dropdownSelect {
  font-family: 'Helvetica Neue';
  font-style: normal;
  font-weight: 500;
  font-size: 14px;
  line-height: 16px;

  color: var(--dropdown_select_text);
  padding: 15px;
  background: var(--dropdown_select_background);
  border-radius: 5px;
  display: flex;
  align-items: center;
  width: calc(100% - 30px);
}

.dropdownSelect img {
  margin-right: 12px;
}

.dropdownSelect:not(:last-child) {
  margin-bottom: 14px;
}

.pay {
  padding: 24px
}

.pay .title {
  font-family: 'Helvetica Neue';
  font-style: normal;
  font-weight: 500;
  font-size: 20px;
  line-height: 22px;

  color: var(--modal_text);
  margin-bottom: 7px;
}

.pay .desc {
  font-family: 'Helvetica Neue';
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 16px;


  color: var(--modal_text_secondary);
  margin-bottom: 24px;
}

.pay .cart {
  margin-bottom: 24px;
}

.pay .cart .cell {
  display: flex;
  text-align: center;
  align-items: center;
}

.pay .cart .cell:not(:last-child) {
  margin-bottom: 10px;
}

.pay .cart .cell img {
  border-radius: 50%;
  width: 30px;
  height: 30px;
  margin-right: 8px;
}

.pay .cart .cell .label {
  display: flex;
  font-family: 'Helvetica Neue';
  font-style: normal;
  font-weight: 500;
  font-size: 14px;
  line-height: 16px;
  text-align: center;

  color: var(--modal_text);
}

.pay .cart .cell .label p {
  margin: 0;
  padding: 0;
  color: var(--modal_text_secondary);
  margin-left: 8px;
}

.pay .cart .cell .amount {
  position: absolute;
  right: 0;
  margin-right: 24px;

  font-family: 'Helvetica Neue';
  font-style: normal;
  font-weight: 500;
  font-size: 14px;
  line-height: 16px;
  text-align: center;

  color: var(--modal_text);
}

.pay .mainButton {
  background: var(--button_color);
  border-radius: 4px;
  font-family: 'Helvetica Neue Bold';
  font-style: normal;
  font-weight: 500;
  font-size: 15px;
  line-height: 17px;

  color: #FFFFFF;
  height: 45px;
  justify-content: center;
  display: flex;
  text-align: center;
  align-items: center;
  width: 100%;
  margin-top: 14px;
  cursor: pointer;
}

.pay .secondaryButton {
  font-family: 'Helvetica Neue';
  font-style: normal;
  font-weight: 500;
  font-size: 15px;
  line-height: 17px;
  text-align: center;

  color: var(--button_text_secondary);
  margin-top: 14px;
  cursor: pointer;
}

@media (min-width: 100px) and (max-width: 410px) {
  .add {
    width: 25px !important;
    height: 25px !important;
    margin-right: 0px !important;
  }

  .unadd {
    margin-left: 0px !important;
    height: 25px !important;
    width: 25px !important;
  }
}

.scanner {
  width: 100%;
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  text-align: center;
  align-items: center;
}

.scanner img {
  width: 99px;
}

.scanner-text {
  margin-top: 24px;
  margin-bottom: 24px;

  font-family: 'Helvetica Neue';
  font-style: normal;
  font-weight: 500;
  font-size: 18px;
  line-height: 22px;
  /* identical to box height */

  text-align: center;

  color: var(--scanner_text);
}

.scanner-text2 {
  margin-top: 8px;
  margin-bottom: 24px;

  font-family: 'Helvetica Neue';
  font-style: normal;
  font-weight: 500;
  font-size: 14px;
  line-height: 18px;
  /* identical to box height */

  text-align: center;

  color: #FFFFFF;
}

.scanner-button {
  background: var(--button_color);
  border-radius: 4px;
  font-family: 'Helvetica Neue';
  font-style: normal;
  font-weight: 500;
  font-size: 15px;
  line-height: 17px;

  color: #FFFFFF;
  height: 45px;
  justify-content: center;
  display: flex;
  text-align: center;
  align-items: center;
  width: fit-content;
  cursor: pointer;
  padding: 0px 24px;
}

.modal-content .add {
  width: 40px !important;
  height: 40px !important;
}

.modal-content .unadd {
  width: 40px !important;
  height: 40px !important;
}