.scrollTopButton {
    width: 30px;
    height: 30px;
    background: var(--dropdown_background);
    border: 2px solid var(--dropdown_border);
    color: #fff;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    border-radius: 50%;
    padding: 16px;
    position: fixed;
    z-index: 999999999999;
    left: calc(100% - 80px);
    bottom: 30px;
}

.scrollTopButton svg {
    fill: var(--dropdown_select_text);
    transform: rotate(180deg);
}