@-webkit-keyframes dropdown-fade-in {
  0% {
    opacity: 0
  }

  to {
    opacity: 1
  }
}

@keyframes dropdown-fade-in {
  0% {
    opacity: 0
  }

  to {
    opacity: 1
  }
}

.dropdown-wrapper {
  position: absolute;
  inset: 0px auto auto 0px;

  transition: opacity 167ms ease-out;
  -webkit-animation: dropdown-fade-in .32s ease;
  animation: dropdown-fade-in .32s ease;
}

.dropdown-content {
  height: fit-content;
  background: var(--dropdown_background);
  border: 2px solid var(--dropdown_border);
  box-sizing: border-box;
  border-radius: 8px;
  padding: 12px;
}