@font-face {
    font-family: "Helvetica Neue";
    src: url(../../assets/HelveticaNeue-Medium-11.ttf) format("truetype");
}

.select {
    background: rgba(255, 255, 255, 0.1);
    border-radius: 5px;
    padding-top: 17.5px;
    padding-left: 18px;
    padding-right: 18px;
    padding-bottom: 17.5px;
    width: calc(100% - 48px - 18px - 18px);
    margin: 24px;
    justify-content: space-between;
    display: flex;
    align-items: center;
}

.select img {
    margin-right: 10px;
}

.selectText {
    font-family: 'Helvetica Neue';
    font-style: normal;
    font-weight: 500;
    font-size: 14px;
    line-height: 17px;
    /* identical to box height */


    color: #FFFFFF;
}

.select .left {
    display: flex;
}