.input {
    background: var(--input_background);
    border-radius: 5px;
    padding-left: 17px;
    padding-right: 17px;
    display: flex;
    align-items: center;
}

.input input {
    margin-left: 9px;
    height: 36px;
    width: calc(100% - 17px - 30px);
    border: none;
    outline: none;
    background: transparent;
    padding: 0;
    color: var(--input_color);
}

.input img {
    width: 20px;
    height: 20px;
    padding-top: 17px;
    padding-bottom: 17px;
}

.input:not(:last-child) {
    margin-bottom: 14px;
}